osition: absolute;\n            bottom: 40px;\n            width: 100%;\n            background-color: colors.$secondary;\n            color: colors.$primary;\n            border-radius: 0.25rem;\n            transition: 0.3s ease;\n            .config_field{\n                display: flex;\n                align-items: center;\n                gap: 12px;\n                padding: 0.75rem 1rem;\n\n                input{\n                    width: 100%;\n                }\n            }\n            &.hide{\n                bottom: -500px;\n                transition: 0.6s ease;\n            }\n        }\n        position: relative;\n        ul{\n            padding-left: 0;\n            display: flex;\n            gap: 12px;\n            li{\n                .bottom_nav_icon{\n                    display: flex;\n                    align-items: center; \n                    cursor: pointer;\n                }\n                list-style: none;\n                color: colors.$primary\n            }\n        }\n    }\n    \n}\n\n\n\n\n\n.activity_selector_nav{\n    display: flex;\n    gap: 12px;\n    margin-bottom: 1rem;\n    select{\n        width: 100%;\n        padding: 0.5rem;\n    }\n}","#landing_nav_title {\n    font-weight: 800;\n    font-size: 48px;\n    line-height: 72px;\n    letter-spacing: -1.5px;\n}\n\nh4{\n    font-family: 'Poppins';\nfont-style: normal;\n    font-weight: 500;\n    font-size: 43px;\n    line-height: 64px;\n    text-align: center;\n    letter-spacing: -1.5px;\n}\n\nh5{\n    font-family: 'Poppins';\n    font-style: normal;\n    font-weight: 500;\n    font-size: 30px;\n    line-height: 45px;\n}\n\nh6{\n    font-family: 'Poppins';\nfont-style: normal;\nfont-weight: 600;\nfont-size: 32px;\nline-height: 48px;\n/* identical to box height */\n\nletter-spacing: -1.5px;\n}\n\np{\n    font-family: 'Poppins';\nfont-style: normal;\nfont-weight: 500;\nfont-size: 24px;\nline-height: 36px;\ntext-align: center;\nletter-spacing: -1.5px;\n}\n\n.caption{\n    font-family: 'Poppins';\nfont-style: normal;\nfont-weight: 400;\nfont-size: 15px;\nline-height: 22px;\n/* identical to box height */\n\nletter-spacing: 0.4px;\n}\n\n.label{\n    font-family: 'Poppins';\nfont-style: normal;\nfont-weight: 400;\nfont-size: 12px;\nline-height: 18px;\n/* identical to box height */\n\nletter-spacing: 0.4px;\n}\n\n.body_1{\n    font-family: 'Poppins';\nfont-style: normal;\nfont-weight: 400;\nfont-size: 20px;\nline-height: 30px;\nletter-spacing: 0.5px;\n}\n\n.txt_left{\n    text-align: left;\n}\n\n.subtile_2{\n    font-family: 'Roboto';\nfont-style: normal;\nfont-weight: 500;\nfont-size: 14px;\nline-height: 16px;\nletter-spacing: 0.1px;\n}\n.showroom_stars_title{\n    font-family: 'Poppins';\n    font-style: normal;\n    font-weight: 600;\n    font-size: 15px;\n    line-height: 22px;\n    letter-spacing: 0.4px;\n}\n.showroom_stars_content{\nfont-family: 'Poppins';\nfont-style: normal;\nfont-weight: 400;\nfont-size: 11px;\nline-height: 16px;\nletter-spacing: 1.5px;\n}\n\n.button{\n    font-family: 'Poppins';\nfont-style: normal;\nfont-weight: 500;\nfont-size: 18px;\nline-height: 27px;\nletter-spacing: 1.25px;\n}\n\n.txt_center{\n    text-align: center;\n}\n\n.txt_800{\n    font-weight: 800;\n}",".flex{\n    display: flex;\n}\n\n.flex_sm_colum{\n    flex-direction: column;\n}",".btn{\n    border: none;\n    padding: 0.75rem 1rem;\n    cursor: pointer;\n}","\n\n.grid{\n    display: grid;\n}\n\n.col_3 {\n    grid-template-columns: repeat(3, 1fr);\n}\n\n.col_6{\n    grid-template-columns: repeat(6, 1fr);\n}\n\n.col_sm_2{\n    grid-template-columns: repeat(2, 1fr);\n}\n\n.span_sm_2{\n    grid-column: span 2;\n}\n\n\n///Create columns for the grid\n@mixin grid_mixin($count, $screenSize) {\n    @for $i from 1 through $count{\n        .col_#{$screenSize}_#{$i} { \n            grid-template-columns: repeat(#{$i}, 1fr);\n         }\n    }\n}\n\n///Create spans for the grid\n@mixin grid_span_mixin($count, $screenSize) {\n    @for $i from 1 through $count{\n        .span_#{$screenSize}_#{$i} { \n            grid-column: span #{$i};\n         }\n    }\n}\n\n///Create gaps for the grid\n@mixin grid_gap_mixin($count, $screenSize, ) {\n    @for $i from 1 through $count{\n        .gap_#{$screenSize}_#{$i*4} { \n            gap: #{$i * 0.25rem};\n         }\n    }\n}\n\n\n//this is xsm - extra small\n@media (min-width: 599px) {\n    @include grid_mixin(12, xsm);\n    @include grid_span_mixin(12, xsm);\n    @include grid_gap_mixin(12, xsm);\n\n    .justify_content_xsm_center{\n        justify-content: center;\n    }\n\n    .alig_items_xsm_center{\n        align-items: center;\n    }\n}\n\n//this is sm - small\n@media (min-width: 904px) {\n    @include grid_mixin(12, sm);\n    @include grid_span_mixin(12, sm);\n    @include grid_gap_mixin(12, sm);\n\n    .justify_content_sm_center{\n        justify-content: center;\n    }\n\n    .alig_items_sm_center{\n        align-items: center;\n    }\n}\n\n//this is md - medium\n@media (min-width: 1239px) {\n    @include grid_mixin(12, md);\n    @include grid_span_mixin(12, md);\n    @include grid_gap_mixin(12, md);\n\n    .justify_content_md_center{\n        justify-content: center;\n    }\n\n    .alig_items_md_center{\n        align-items: center;\n    }\n}\n\n//this is large\n@media (min-width: 1440px) {\n    @include grid_mixin(12, lg);\n    @include grid_span_mixin(12, lg);\n    @include grid_gap_mixin(12, lg);\n\n    .justify_content_lg_center{\n        justify-content: center;\n    }\n\n    .alig_items_lg_center{\n        align-items: center;\n    }\n}","@use \"colors\";\n\n.landing_card_body{\n    display: flex;\n    padding: 2rem 2rem;\n    gap: 1.5rem;\n    .landing_card_icon{\n        align-self: center;\n        span{\n            font-size: 80px !important;\n        }\n\n    }\n}\n\n.smallCard_body{\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n\n    padding: 2rem 0.75rem;\n\n    .smallCard_icon{\n        .material-symbols-outlined{\n            font-size: 48px;\n        }\n    }\n}\n\n.apu_card{\n    background-color: colors.$tertiary;\n    padding: 0.25rem 1rem;\n    border-radius: 0.5rem;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    margin-bottom: 12px;\n    .apu_card_title{\n            font-size: 1rem;\n            text-align: left;\n            line-height: 1.5rem;\n    }\n    .apu_card_description{\n        font-size: 0.75rem;\n        font-weight: 500;\n        text-align: left;\n        letter-spacing: 0.4px;\n        line-height: 1rem;\n    }\n    .apu_card_actions{\n        display: flex;\n        gap: 12px;\n        span{\n            display: flex;\n            align-items: center;\n            cursor: pointer;\n        }\n    }\n}\n\n.showroom_item_container{\n    display: flex;\n\n    .showroom_title_container{\n        display: flex;\n        gap: 0.5rem;\n        .showroom_date_container{\n            background-color: colors.$secondary;\n            color: colors.$primary;\n            padding: 0.75rem;\n        }\n        .showroom_title{\n            p{\n                text-align: left;\n            }\n        }\n    }\n\n    .showroom_description{\n        margin: 1rem 0 1rem;\n        p{\n            text-align: left;\n        }\n    }\n\n    .showroom_calification{\n        display: flex;\n        gap: 12px;\n        ul{\n            padding-left: 0;\n            display: flex;\n            align-items: center;\n            li{\n                list-style: none;\n            }\n        }\n    }\n\n    .showroom_contractor{\n        p{\n            text-align: left;\n        }\n    }\n}\n\n.showroom_stars_container{\n    background-color: colors.$secondary;\n    color: colors.$primary;\n    display: flex;\n    border-radius: 4px;\n    align-items: center;\n    padding: 0.75rem;\n    gap: 12px;\n    .showroom_starts_image{\n        img{\n            width: 56px;\n            aspect-ratio: 1/1;\n            object-fit: cover;\n            border-radius: 50%;\n        }\n    }\n\n}\n",".my_sm_16{\n    margin-top: 16px;\n    margin-bottom: 16px;\n}","@use \"colors\";\n\n.layout_grid {\n    gap: 12px;\n    min-height: 100vh;\n    display: grid;\n    grid-template-columns: auto 1fr;\n    .layout_left_navbar {\n        background-color: colors.$secondary;\n        width: 300px;\n        transition: 0.5s ease;\n        &.minimized{\n            width: 72px;\n            transition: 0.5s ease;\n            .top_panel {\n                justify-content: center;\n                img{\n                    display: none;\n                }\n                .top_panel_search{\n                    display: none;\n                }\n            }\n\n\n            .user_section {\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                img{\n                    width: 30px;\n                    transition: 0.3s ease;\n                }\n                p{\n                    display: none;\n                }\n            }\n\n            .left_nav_section{\n                .interests_title{\n                    display: none;\n                }\n\n                .layout_navbar_list{\n                    a{\n                        display: flex;\n                        justify-content: center;\n                    }\n                    p{\n                        display: none;\n                    }\n                }\n            }\n        }\n\n        .left_navbar_container{\n            position: fixed;\n            width: inherit;\n            padding: 2rem 0rem;\n        }\n        \n        .top_panel {\n            display: flex;\n            gap: 12px;\n            align-items: center;\n            padding: 0.75rem 2rem;\n\n            .top_panel_search{\n                width: 100%;\n                input{\n                    width: inherit;\n                    padding: 0.75rem;\n                }\n            }\n\n            span{\n                cursor: pointer;\n            }\n        }\n\n        .user_section {\n            display: flex;\n            align-items: center;\n            gap: 1rem;\n            padding: 0.75rem 2rem;\n            img{\n                width: 30px;\n                transition: 0.3s ease;\n            }\n        }\n\n        .interests_title{\n            padding: 0.75rem 2rem;\n            p{\n                text-align: left;\n            }\n        }\n\n        .layout_navbar_list {\n            ul {\n                padding-left: 0;\n\n                li {\n                    list-style: none;\n\n                    a {\n                        display: flex;\n                        gap: 0.75rem;\n                        align-items: center;\n\n                        color: colors.$primary;\n                        text-decoration: none;\n                        //padding\n                        padding: 0.75rem 2rem;\n\n                        p{\n                                                    //Font \n                        font-family: 'Poppins';\n                        font-style: normal;\n                        font-weight: 700;\n                        font-size: 15px;\n                        line-height: 22px;\n                        letter-spacing: 0.4px;\n\n                        }\n                    }\n                }\n            }\n        }\n    }\n\n    .right_section {\n        padding: 1rem 1rem 1rem 0;\n        position: relative;\n        .layout_top_navbar{\n            display: grid;\n            grid-template-columns: repeat(3, 1fr);\n            gap: 12px;\n            .main_middle_navbar{\n                grid-column: 1/3;\n                background-color: colors.$secondary;\n                display: flex;\n                justify-content: center;\n                align-items: center;\n                border-radius: 0.5rem;\n                ul{\n                    padding-left: 0;\n                    display: flex;\n                    width: 100%;\n                    justify-content: space-around;\n                    li{\n                        list-style: none;\n                        display: flex;\n                        a{\n                            text-decoration: none;\n                            color: colors.$primary;\n                            text-align: center;\n                        }\n                    }\n                }\n            }\n            .main_right_navbar{\n                background-color: colors.$secondary;\n                border-radius: 0.5rem;\n                padding: 1rem 1.5rem;\n                .user_options_navbar{\n                    display: flex;\n                    gap: 0.75rem;\n                    align-items: center;\n                    img{\n                        width: 50px;\n                        aspect-ratio: 1/1;\n                    }\n                }\n            }\n        }\n    }\n}\n\n.results_container{\n    height: 75vh;\n    overflow: auto;\n}\n\n.min_height_100 {\n    min-height: 100vh;\n}\n\n.add_container{\n    background-color: colors.$primary;\n    padding: 1rem 1.5rem;\n    border-radius: 0.5rem;\n}","@use \"colors\";\n\n.table_container {\n    overflow-x: auto;\n}\n\n.cidein_table {\n    border-collapse: collapse;\n    width: 100%;\n    td {\n        padding: 0.75rem 0.5rem;\n\n        //Fonts\n        font-style: normal;\n        font-weight: 500;\n        font-size: 14px;\n        line-height: 16px;\n        letter-spacing: 0.1px;\n    }\n\n    thead {\n        td {\n            background-color: colors.$secondary;\n            color: colors.$primary;\n            &.border_top_right {\n                border-top-right-radius: 0.5rem;\n            }\n\n            &.border_top_left {\n                border-top-left-radius: 0.5rem;\n            }\n        }\n    }\n\n    tbody {\n        .activity_name {\n            background-color: colors.$tertiary;\n\n            .activity_header{\n                display: flex;\n                justify-content: space-between;\n                align-items: center;\n                .activity_name_index{\n                    display: flex;\n                }\n                .activity_options{\n                    .delete_activity{\n                        color: colors.$secondary;\n                        transition: 0.1s ease;\n                        cursor: pointer;\n                        &:hover{\n                            color: rgb(255, 25, 25);\n                            transition: 0.3s ease;\n                        }\n                    }\n                }\n            }\n        }\n\n        .subActivity_row {\n            border-bottom: solid 1px black;\n        }\n\n        .subtotal_activity {\n            .subtotal_price {\n                text-align: right;\n            }\n        }\n    }\n}\n\n.table_index{\n    font-weight: 500;\n    font-size: 15px;\n    line-height:30px;\n    margin-right: 0.5rem;\n}\n\n.subActivity_options{\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    transition: 0.3s ease-in-out;\n    span{\n        font-size: 1rem;\n        &:hover{\n            cursor: pointer;\n            color: rgb(255, 40, 40);\n            transition: 0.2s ease-in-out;\n        }\n    }\n}","@use \"colors\"; \n\n.table_input{\n    width: 15ch;\n    height: 100%;\n    border: none;\n}\n\n.table_name_input{\n    width: 40ch;\n    background-color: colors.$tertiary;\n    border: none;\n\n    //Font\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 22px;\n}\n\n.title_input{\n    width: 100%;\n    border: 2px dashed colors.$secondary;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 30px;\n    line-height: 45px;\n}\n\n.unit_input{\n    width: 8ch;\n    border: 2px dashed colors.$secondary;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 30px;\n    line-height: 45px;\n}\n\n.editable_input{\n    border: 2px dashed colors.$secondary;\n}\n.description_input{\n    width: 100%;\n    border: 2px dashed colors.$secondary;\n}\n\n.search_box{\n    display: flex;\n    gap: 12px;\n\n    input{\n        width: 100%;\n        padding: 0 1rem;\n    }\n}\n\n.chapter_selector{\n    width: 100%;\n    background-color: white;\n    border: 2px dashed colors.$secondary;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 30px;\n    line-height: 45px;\n}\n\n.cidein_form{\n    .form_input_container{\n        display: flex;\n        flex-direction: column;\n        margin-bottom: 12px;\n        input{\n            border-radius: 4px;\n            padding: 0.75rem 0.75rem;\n            border: none;\n        }\n    }\n}\n\n.cidein_form_single{\n    .form_input_container{\n        display: flex;\n        flex-direction: column;\n\n        input{\n            padding: 0.25rem 0.25rem;\n        }\n    }\n}\n\n.form_100{\n    width: 100%;\n}","@use \"colors\";\n\n.pill{\n    background-color: colors.$secondary;\n    color: colors.$primary;\n    display: flex;\n    border-radius: 0.5rem;\n    padding: 0.25rem 0.75rem;\n    transition: all 0.3s ease;\n\n    &.active{\n        background-color: colors.$primary;\n        color: colors.$secondary;\n    }\n\n    &:hover{\n        background-color: colors.$primary;\n        color: colors.$secondary;\n        transition: all 0.5s ease;\n        cursor: pointer;\n    }\n\n    p{\n        text-transform: uppercase;\n        font-size: 0.9rem;\n    }\n    span{\n        display: flex;\n        align-items: center;\n        margin-right: 0.5rem;\n    }\n}",".warning {\n    position: fixed;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 0.5rem 1rem;\n    width: 100%;\n    top: 0;\n    left: 0;\n    transition: 0.5s ease;\n\n    .warning_info {\n        display: flex;\n        gap: 12px;\n    }\n\n    .warning_option{\n        span{\n            cursor: pointer;\n        }\n    }\n\n    .warning_container {\n        display: flex;\n    }\n\n    &.close {\n        top: -100px;\n        transition: 0.5s ease\n    }\n}","@use \"colors\";\n\n.spinner_home{\n  display: flex;\n  justify-content: center;\n}\n\n.spinner-cidein {\n    display: inline-block;\n    position: relative;\n    width: 80px;\n    height: 80px;\n  }\n  .spinner-cidein div {\n    display: inline-block;\n    position: absolute;\n    left: 8px;\n    width: 16px;\n    background: colors.$primary;\n    animation: spinner-cidein 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;\n  }\n  .spinner-cidein div:nth-child(1) {\n    left: 8px;\n    animation-delay: -0.24s;\n  }\n  .spinner-cidein div:nth-child(2) {\n    left: 32px;\n    animation-delay: -0.12s;\n  }\n  .spinner-cidein div:nth-child(3) {\n    left: 56px;\n    animation-delay: 0;\n  }\n  @keyframes spinner-cidein {\n    0% {\n      top: 8px;\n      height: 64px;\n    }\n    50%, 100% {\n      top: 24px;\n      height: 32px;\n    }\n  }","@use \"navbar\";\n@use \"fonts\";\n@use \"flex\";\n@use \"buttons\";\n@use \"grid\";\n@use \"cards\";\n@use \"spacing\";\n@use \"layout\";\n@use \"tables\";\n@use \"forms\";\n@use \"pills\";\n@use \"warnings\";\n@use \"colors\";\n@use \"spinners\";\n\n@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600;800;900&display=swap');\n\n*{\n    margin: 0;\n    font-family: 'Poppins', sans-serif;\n    box-sizing: border-box;\n}\n\n#banner{\n    position: relative;\n    img{\n        width: 100%;\n    }\n\n    #over_banner_info{\n        position: absolute;\n        top: 0;\n        left: 0;\n        width: 100%;\n        height: 100%;\n        background-color: rgba(39, 39, 39, 0.76);\n\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        align-items: center;\n\n        p{\n            width: 60%;\n        }\n    }\n}\n\n.cidein_footer{\n    padding: 2rem 0.75rem;\n    p{\n        font-size: 1rem;\n       